import React from 'react';
import { Link } from 'react-router-dom';
import { makeStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import Grid from '@material-ui/core/Grid';
import Container from '@material-ui/core/Container';
import JobListingCard from './JobListingCard';
import Obj_logo from "./images/LOGO_OBJ.svg";
const useStyles = makeStyles((theme) => ({
  appBar: {
    marginBottom: theme.spacing(3),
  },
  title: {
    flexGrow: 1,
  },
  adminButton: {
    marginLeft: theme.spacing(2),
  },
  jobListingsContainer: {
    display: 'flex',
    justifyContent: 'center',
    marginTop: theme.spacing(3),
  },
  Container:{
    display: 'flex',
    justifyContent: 'center',
    height:'80vh'
  },
  titleText: {
    fontFamily: 'Arial, sans-serif',
    fontSize: '2.3rem',
    fontWeight: 'bold',
    display: 'flex',
    justifyContent: 'center'
  },
}));

const HomePage = () => {
  const classes = useStyles();

  const jobListings = [
    {
      title: 'Job Title 1',
      description: 'Job Description 1',
      salary: '$100,000',
      experience: '5 years',
    },
    {
      title: 'Job Title 2',
      description: 'Job Description 2',
      salary: '$80,000',
      experience: '3 years',
    },
    {
      title: 'Job Title 3',
      description: 'Job Description 3',
      salary: '$120,000',
      experience: '8 years',
    },
    {
      title: 'Job Title 4',
      description: 'Job Description 4',
      salary: '$90,000',
      experience: '4 years',
    },
    {
      title: 'Job Title 5',
      description: 'Job Description 5',
      salary: '$110,000',
      experience: '6 years',
    },
  ];

  return (
    <div>
      <AppBar position="static" className={classes.appBar}>
        <Toolbar>
          <Typography variant="h6" className={classes.title}>
            <Link to="/">
            <img src={Obj_logo} alt="Logo" className='logo' />
            </Link>
          </Typography>
          <Button component={Link} to="/" color="inherit" className={classes.adminButton}>
            Admin Login
          </Button>
        </Toolbar>
      </AppBar>
      <Container>
        <Typography variant="h3" className={classes.titleText} component="h1" align="center" gutterBottom>
          AVAILABLE JOBS 
        </Typography>
        <div className={classes.jobListingsContainer}>
          <Grid container spacing={3}>
            {jobListings.map((job, index) => (
              <JobListingCard key={index} job={job} />
            ))}
          </Grid>
        </div>
      </Container>
    </div>
  );
};

export default HomePage;
