import React, { useState, useRef } from 'react';
import axios from 'axios';
import { Link } from 'react-router-dom';
import { makeStyles } from '@material-ui/core/styles';
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import Grid from '@material-ui/core/Grid';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import Swal from 'sweetalert2';
import withReactContent from 'sweetalert2-react-content';
import Obj_logo from "./images/LOGO_OBJ.svg";

const MySwal = withReactContent(Swal);

const useStyles = makeStyles((theme) => ({
  container: {
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
    height: '82vh',
  },
  formCard: {
    maxWidth: '800px',
    padding: theme.spacing(6),
    boxShadow: '0px 2px 10px rgba(0, 0, 0, 0.1)',
    borderRadius: '8px',
  },
  submitButton: {
    alignSelf: 'center',
  },
  appBar: {
    marginBottom: theme.spacing(3),
  },
  title: {
    flexGrow: 1,
  },
  adminButton: {
    marginLeft: theme.spacing(2),
  },
  textField: {
    marginBottom: theme.spacing(2),
    paddingTop: theme.spacing(1),
    paddingBottom: theme.spacing(1),
  },
  titleText: {
    fontFamily: 'Arial, sans-serif',
    fontSize: '2rem',
    fontWeight: 'bold',
    display: 'flex',
    justifyContent: 'center'
  },
  
}));

const JobApplicationForm = () => {
  const classes = useStyles();
  const formRef = useRef(null);

  const [formData, setFormData] = useState({
    name: '',
    address: '',
    experience: '',
    previousCompany: '',
    noticePeriod: '',
    lastCTC: '',
    expectedCTC: '',
    phoneNO: '',
  });

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData({ ...formData, [name]: value });
  };

  const handleFileChange = (e) => {
    setFormData({ ...formData, resume: e.target.files[0] });
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
      const response = await axios.post('https://jobsinobjectways.onrender.com/api/apply-job', formData);
      console.log(response.data); // Handle response data as per your requirement
      MySwal.fire({
        icon: 'success',
        title: 'Application Submitted',
        text: 'Your job application has been successfully submitted.',
      }).then(() => {
        formRef.current.reset();
      });
    } catch (error) {
      console.error(error);
    }
    window.location = "/"
  };

  const validatePhoneNumber = (phoneNO) => {
    const phoneNoRegex = /^[0-9]{10}$/;
    return phoneNoRegex.test(phoneNO);
  };

  const handleFormSubmit = (e) => {
    e.preventDefault();
    if (!formData.phoneNO || !validatePhoneNumber(formData.phoneNO)) {
      MySwal.fire({
        icon: 'error',
        title: 'Invalid Phone Number',
        text: 'Please enter a valid 10-digit phone number.',
      });
      return;
    }
    handleSubmit(e);
  };

  return (
    <div>
      <AppBar position="static" className={classes.appBar}>
        <Toolbar>
          <Typography variant="h6" className={classes.title}>
            <Link to="/">
              <img src={Obj_logo} alt="Logo" className='logo' />
            </Link>
          </Typography>
          <Button component={Link} to="/" color="inherit" className={classes.adminButton}>
            Admin Login
          </Button>
        </Toolbar>
      </AppBar>
      <h2 className={classes.titleText}>JOB APPLY FORM</h2>
      <div className={classes.container}>
        <Card className={classes.formCard}>
     
          
          <CardContent>
           
            <form ref={formRef} onSubmit={handleFormSubmit}>
            <Grid container spacing={2}>
                <Grid item xs={12} sm={6}>
                  <TextField
                  className={classes.textField}
                    label="Name"
                    name="name"
                    value={formData.name}
                    onChange={handleChange}
                    variant="standard"
                    fullWidth
                    required
                  />
                </Grid>
                <Grid item xs={12} sm={6}>
                  <TextField
                  className={classes.textField}
                    label="Address"
                    name="address"
                    value={formData.address}
                    onChange={handleChange}
                    variant="standard"
                    fullWidth
                    required
                  />
                </Grid>
                <Grid item xs={12} sm={6}>
                  <TextField
                  className={classes.textField}
                    label="Experience"
                    name="experience"
                    type="number"
                    value={formData.experience}
                    onChange={handleChange}
                    variant="standard"
                    fullWidth
                    required
                  />
                </Grid>
                <Grid item xs={12} sm={6}>
                  <TextField
                  className={classes.textField}
                    label="Previous Company"
                    name="previousCompany"
                    value={formData.previousCompany}
                    onChange={handleChange}
                    variant="standard"
                    fullWidth
                    required
                  />
                </Grid>
                <Grid item xs={12} sm={6}>
                  <TextField
                  className={classes.textField}
                    label="Last CTC (In LPA)"
                    name="lastCTC"
                    value={formData.lastCTC}
                    onChange={handleChange}
                    variant="standard"
                    fullWidth
                    required
                  />
                </Grid>
                <Grid item xs={12} sm={6}>
                  <TextField
                  className={classes.textField}
                    label="Expected CTC (In LPA)"
                    name="expectedCTC"
                    value={formData.expectedCTC}
                    onChange={handleChange}
                    variant="standard"
                    fullWidth
                    required
                  />
                </Grid>
                <Grid item xs={12} sm={6}>
                  <TextField
                  className={classes.textField}
                    label="Notice Period"
                    name="noticePeriod"
                    value={formData.noticePeriod}
                    onChange={handleChange}
                    variant="standard"
                    type="number"
                    fullWidth
                    required
                  />
                </Grid>
                <Grid item xs={12} sm={6}>
                  <TextField
                  className={classes.textField}
                    label="Phone NO"
                    name="phoneNO"
                    value={formData.phoneNO}
                    onChange={handleChange}
                    variant="standard"
                    type="tel"
                    fullWidth
                    required
                    pattern="[0-9]{10}"
                  />
                </Grid>
              </Grid>
              <div className="d-flex justify-content-center mt-5">
                <Button className={classes.submitButton} type="submit" variant="contained" color="primary">
                  Submit
                </Button>
              </div>
            </form>
          </CardContent>
        </Card>
      </div>
    </div>
  );
};

export default JobApplicationForm;
